<template>
  <v-container fluid class="down-top-padding">
    <v-row>
      <input type="file" name="" class="d-none"  ref="files" @change="fileInput" id="">
      <v-col cols="12" lg="4">
        <v-card>
          <v-card-text class="text-center pa-7">
           <v-row>
                  <v-col cols="12" class="text-right">

      <v-btn v-if="child.length!=3 && !parentProcessing" class="mx-2 " small @click="setNewChild()" fab dark color="primary">
      <v-icon dark>mdi-plus</v-icon>
    </v-btn>
                  </v-col>
             <v-col :cols="child.length==1?12:child.length==2?6:child.length==3?4:12" v-for="(item, index) in child" :key="index">
                    <v-btn v-if="child.length!=1 && !parentProcessing" class="mx-2 " x-small @click="removeChild(index)" fab dark color="danger">
      <v-icon dark>mdi-minus</v-icon>
    </v-btn>
    <v-list-item-avatar :size="child.length==2?130:child.length==3?100:170" color="grey darken-3">
          <span @click="activeChild=index" >
            <v-img
            v-if="colorShow"
            class="elevation-6"

            :src="child[index].files==null?'https://s3.amazonaws.com/37assets/svn/765-default-avatar.png':child[index].files"
             ></v-img>
          </span>
        </v-list-item-avatar>
  <div class="text-center">
            <v-btn class="text-capitalize mt-5 element-0" style="color:white" x-small  @click="activeChild=index;$refs['files'].click()" color="danger"> + Resim {{child[index].files!=null?'Düzenle':'Ekle'}}</v-btn>
  <div class="mt-2">  <span v-if="activeChild==index&&child.length>1" class="success--text">Aktif</span></div>
  </div>
            <h4 class="mt-2 title blue-grey--text text--darken-2 font-weight-regular">{{item.name}} {{item.lastname}}</h4>
            <h6 class="subtitle-2 font-weight-light">{{$t('header.child')}}</h6>
             </v-col>
           </v-row>
           <div v-if="parentShow">
            <v-divider class="my-3"></v-divider>
            <p v-if="parentStatus"><small @click="parentStatus=false;parentProcessing=true" class="text-danger" style="cursor:pointer">Veliyi Tekrar Düzenle <v-icon>mdi-square-edit-outline</v-icon></small> </p>
             <h4>{{$t('parent.parentInfo')}} </h4>
             <p class="mt-2" v-if="parent.name!='' || parent.lastname!=''">
              <strong>{{$t('parent.name')}}</strong>  : {{parent.name}} {{parent.lastname}}
             </p>
             <p class="mt-2" v-if="parent.username!=''">
              <strong> {{$t('generate.username')}}</strong> : {{parent.username}}
             </p>
             <p  class="mt-2" v-if="parent.password!=''">
               <strong> {{$t('generate.password')}} </strong> : <span v-for="(item, index) in parent.password.length" :key="index">*</span>
             </p>
             <p class="mt-2" v-if="parent.phone!=''">
               <strong>{{$t('generate.phone')}} </strong> : {{parent.phone}}
             </p>
           </div>
             <div>
               <v-alert
               v-if="alert.length!=0"
      dense
      prominent
      text
      :type="alert[0].to==-1?'success':'error'"
    >
    <ul>
      <li style="padding:4px" v-for="(item,index) in alert" :key="index"><small>{{item.err}} </small>
      <p><small> <span v-if="item.to!=-1" @click="activeChild=item.to" style="text-decoration:underline;cursor:pointer">Öğrenciyi Düzenle</span></small></p></li>
    </ul>
    </v-alert>
              </div>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12" lg="8">
        <v-card v-if="parentProcessing">
          <v-card-text>
            <h3 class="title blue-grey--text text--darken-2 font-weight-regular">{{$t('dashboard.insertParent')}}</h3>
            <h6 class="subtitle-2 font-weight-light">{{$t('parent.info')}}</h6>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-text>
            <v-text-field
              :error-messages="errName"
              v-model="parent.name"
              :label="$t('parent.name')"
              filled
              background-color="transparent"
            ></v-text-field>
               <v-text-field
                :error-messages="errLastname"
              v-model="parent.lastname"
               :label="$t('parent.lastname')"
              filled
              background-color="transparent"
            ></v-text-field>
            <v-text-field
              type="text"
              v-model="parent.username"
             :label="$t('generate.username')"
             :error-messages="errormessages"
             @blur="controlUsername"
             :success-messages="successmessages"
              filled
              background-color="transparent"
            ></v-text-field>
            <v-text-field
              v-model="parent.password"
              filled
               :error-messages="errPassword"
              background-color="transparent"
              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              :type="show1 ? 'text' : 'password'"
              name="input-10-1"
                :label="$t('generate.password')"
              :hint="$t('err.password')"
              counter
              @click:append="show1 = !show1"
            ></v-text-field>
           <v-text-field
              type="tel"
              @blur="phoneControl()"
               :error-messages="errPhone"
              v-model="parent.phone"
             :label="$t('generate.phone')"
              filled
              background-color="transparent"
            ></v-text-field>

            <v-btn  :loading="loading" :disabled="loading" class="text-capitalize mt-5 element-0" @click="!loading?setParent():''" color="success">{{$t('generate.submit')}}</v-btn>
          </v-card-text>
        </v-card>
          <v-card v-else>
          <v-card-text>
            <h3 class="title blue-grey--text text--darken-2 font-weight-regular">{{$t('dashboard.insertStudent')}}</h3>
            <h6 class="subtitle-2 font-weight-light">{{$t('child.info')}}</h6>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-text>
            <v-text-field
              v-model="child[activeChild].name"
              :label="$t('child.name')"
              filled
              background-color="transparent"
            ></v-text-field>
               <v-text-field
              v-model="child[activeChild].lastname"
               :label="$t('child.lastname')"
              filled
              background-color="transparent"
            ></v-text-field>
             <p>Cinsiyet Seçiniz : </p>
                 <v-radio-group v-model="child[activeChild].isBoy" row>
      <v-radio label="Erkek" :value="1"></v-radio>
      <v-radio label="Kız" :value="0"></v-radio>
    </v-radio-group>
            <v-date-picker   locale="tr" v-model="child[activeChild].birthday"></v-date-picker>
               <v-select
               class="mt-2"
               v-model="child[activeChild].teacherId"
          :items="teacher"
           item-text="fullName"
  item-value="id"
         :label="$t('generate.selectTeacher')"
        ></v-select>
          <v-select
          v-if="hostVisible"
               class="mt-2"
               v-model="child[activeChild].hostId"
          :items="host"
           item-text="fullname"
  item-value="id"
         :label="$t('generate.selectHost')"
        ></v-select>

            <v-btn :loading="loadingChild" :disabled="loadingChild" class="text-capitalize mt-5 element-0" @click="sendParent()" color="success">{{$t('generate.submit')}}</v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import {phoneNumberControl} from '../../plugins/rules'
export default {
  name: "Profile",
  computed:{
    parentShow(){
      if(this.parent.name!=''||this.parent.lastname!=''||this.parent.username!=''||this.parent.password||this.parent.phone!=''){
        return true
      }
      else{
        return false
      }
    }
  },
  data: () => ({
    loadingChild:false,
    teacher:[],
    alert:[],
    loading:false,
    errormessages:[],
    errPhone:[],
    errName:[],
    errLastname:[],
    errPassword:[],
    successmessages:[],
    parentProcessing:true,
    parent:{
      name:"",
      lastname:"",
      username:"",
      password:"",
      phone:""
    },
    activeChild:0,
    colorShow:true,
      child:[
        {
      name:"",
      lastname:"",
      birthday:"",
      bday:"",
      phone:"",
      hostId:null,
      isBoy:0,
      teacherId:null,
      parentId:null,
      image:[],
      file:null
    }
      ],
    dtext: "George deo",
    emailtext: "",
    password: "",
    disableinput: "",
    checkbox1: "",
    checkbox2: "",
    checkbox3: "",
    parentStatus:false,
    show1: false,
    host:[],
    hostVisible:false,
    rules: {
      required: value => !!value || "Required.",
      min: v => v.length >= 8 || "Min 8 characters",
      emailMatch: () => "The email and password you entered don't match"
    },
    items: ["London", "India", "America"]
  }),
  methods:{
     phoneControl(){
    let result = phoneNumberControl(this.parent.phone)
    if(result){
      this.errPhone.push(result)
    }
    else{
      this.errPhone=[]
    }
  },
    setParent(){
      this.errName=[]
      this.errLastname=[]
      this.errPassword=[]
      if(this.parent.name==''||this.parent.lastname==''||this.parent.username.length<2||this.parent.password.length<5||this.parent.phone.length<9||this.errPhone.length!=0||this.errormessages.length!=0){
        if(this.parent.name==''){
          this.errName.push('Veli adı boş geçilemez.')
        }
        if(this.parent.lastname==''){
          this.errLastname.push('Veli soyadı boş geçilemez.')
        }
        if(this.parent.username.length<2){
          this.errormessages.push('Kullanıcı adı 3 karakterden aşağı olamaz.')
        }
        if(this.parent.password.length<5){
          this.errPassword.push('Şifreniz en az 6 karakter olmalıdır.')
        }
        if(this.parent.phone.length<9){
          this.errPhone.push('Eksik veya yanlış bir telefon numarası girdiniz.')
        }
                   this.parentStatus=false
      }
      else{
        this.loading=true
         this.$api.get('user?filter=isTeacher:1&limit=100').then(res=>{
         if(res.count!=0){
           this.teacher = res.body
           res.body.forEach(res=>{
             res.fullName = res.name + ' ' + res.lastName
           })
           this.teacher = res.body
           this.$api.get('host?limit=100').then(host=>{
             if(host.count==0){
               this.hostVisible=false
             }
             else{
               this.host = host.body
                 this.hostVisible=true
             }
              this.loading=false
           this.parentProcessing=false
           this.parentStatus=true
           })

         }
        })

      }
    },
    controlUsername(){
    this.loading=true
      this.$api.get('h/controlUsername?username="'+this.parent.username+'"').then(res=>{
        if(res.count==0){
            this.successmessages=[]
                this.errormessages=[]
          this.successmessages.push('Bu kullanıcı adı kullanılabilir.')
           this.loading=false
        }
        else{
            this.successmessages=[]
          this.errormessages=[]
          this.errormessages.push(`${this.parent.username} adlı kullanıcı ismi kullanılıyor.`)
           this.loading=false

        }
      })
    },
      createImage(file) {

      var reader = new FileReader();
      var vm = this;
      reader.onload = e => {
        vm.child[vm.activeChild].files = e.target.result;
             this.colorShow=true
      };
      reader.readAsDataURL(file);


    },
    fileInput(event){
      this.colorShow=false
       this.child[this.activeChild].images = event.target.files[0]
       this.createImage(this.child[this.activeChild].images)
    },
    setNewChild(){
      this.child.push(
          {
      name:"",
      lastname:"",
      birthday:"",
      phone:"",
       isBoy:0,
           teacherId:null,
      parentId:null,
          file:null,
          hostId:null
    }
      )
    },
    removeChild(index){
      this.child.splice(index,1)
    },
   async sendParent(){
     this.loadingChild=true
     this.alert=[]
     if(this.parentStatus){
       this.child.forEach(async (ch,index)=>{
       if(ch.name==''||ch.lastname==''||ch.birthday==''||ch.teacherId==null||ch.images===undefined){
         if(ch.name==''){
           this.alert.push({err:(index+1)+'. öğrencinin ismi boş geçilemez!',to:index})
         }
         if(ch.lastname==''){
           this.alert.push({err:(index+1)+'. öğrencinin soyadı boş geçilemez!',to:index})
         }
         if(ch.birthday==''){
           this.alert.push({err:(index+1)+'. öğrencinin doğum tarihi boş geçilemez!',to:index})
         }
         if(ch.teacherId==null){
           this.alert.push({err:(index+1)+'. öğrencinin öğretmeni seçilmedi!',to:index})
         }
           if(ch.images===undefined){
           this.alert.push({err:(index+1)+'. öğrencinin fotoğrafı seçilmedi!',to:index})
         }
       }

       })

    if(this.alert.length==0){
       let result = await this.$api.post('user',this.parent)
   if(result.id){
     this.child.forEach(async (ch,index)=>{
         let fd = new FormData()
       fd.append('image',ch.images);
       fd.append('name',ch.name);
       fd.append('lastname',ch.lastname);
       fd.append('birthday',ch.birthday);
       fd.append('isBoy',ch.isBoy);
       fd.append('teacherId',ch.teacherId);
       fd.append('hostId',ch.hostId);
       fd.append('parentId',result.id)
       let cResponse = await this.$api.postfd('student','image',fd)
        if(cResponse.status=='success'){
          this.alert.forEach((alrt,alertIndex)=>{
            if(alrt.to==index){
              this.alert.splice(alertIndex,1)
            }
          })
          this.alert.push({err:(index+1)+'. öğrenci başarıyla eklendi.',to:-1})
        }
       if(this.child.length-1==index){
         this.activeChild=0
         this.child=[
           {name:"",
      lastname:"",
      birthday:"",
      phone:"",
       isBoy:0,
           teacherId:null,
      parentId:null,
          file:null}
         ]
         this.parent={
            name:"",
      lastname:"",
      username:"",
      password:"",
      phone:""
         }
         this.parentProcessing=true
     this.loadingChild=false


       }
     })
   }
    }
    else{
        this.loadingChild=false
    }
     }
     else{
       this.alert.push({err:'Önce Veliyi onaylayınız!',to:0})
     }

    }
  }
};
</script>

<style>
.active{
  border : 1px solid seagreen;
}
</style>
